.fadeIn {
    -webkit-animation: fadeIn ease-in var(--anim-duration);
    animation: fadeIn ease-in var(--anim-duration);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-duration: var(--anim-duration);
    animation-duration: var(--anim-duration);
}

.wrapper {
    width: 100%;
    height: calc(100vh - 65px);
    --anim-duration: 1s;
    --edgeLine-opacity: 1;
    > .edgeLine {
        position: absolute;
        @extend .fadeIn;
        width:400px;
        height: 1px;
        transform: rotate(-45deg);
    }

    > .edgeLine:nth-child(1) {
        background-color: #a5a5a5;
        transform: rotate(-45deg) translateX(70px);
        top: -80px;
        bottom: 0;
        left: -140px;
        margin: auto;
    }

    > .edgeLine:nth-child(2) {
        background-color: #707070;
        top: 0;
        bottom: 0;
        left: -210px;
        margin: auto;
    }

    > .edgeLine:nth-child(3) {
        background-color: #707070;
        top: 100px;
        bottom: 0;
        right: -80px;
        margin: auto;
    }

    > .edgeLine:nth-child(4) {
        background-color: #a5a5a5;
        transform: rotate(-45deg) translateX(70px);
        top: 120px;
        bottom: 0;
        right: -100px;
        margin: auto;
    }
}

.container {
    height: 100%;
    padding: 0 10vw;
    max-width: 1500px;
    margin: auto;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    align-items: left;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.buttonContainer {
    @extend .fadeIn;
    animation-delay: 0.7s;
    display: flex;
    margin: 40px 0;
    width: max-content;
    justify-content: center;
    opacity: 0;
}

.button_left, .button_right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 5px;
    margin-bottom: 5px;
}

.button_left {
    align-items: flex-end;
    margin-right: 25px;
}

.button_right {
    align-items: flex-start;
    margin-left: 25px;
}

.button_lineSmall {
    --anim-duration: 1.5s;
    height: 2px;
    width: 0;
    max-width: 240px;
    background-color: #DB931E;
    -webkit-animation: expandInSmall ease-in-out var(--anim-duration);
    animation: expandInSmall ease-in-out var(--anim-duration);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-duration: var(--anim-duration);
    animation-duration: var(--anim-duration);
    animation-delay: 1s;
}

.button_lineLarge {
    --anim-duration: 1.5s;
    height: 2px;
    width: 0;
    max-width: 380px;
    background-color: #DB931E;
    -webkit-animation: expandInLarge ease-in-out var(--anim-duration);
    animation: expandInLarge ease-in-out var(--anim-duration);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-duration: var(--anim-duration);
    animation-duration: var(--anim-duration);
    animation-delay: 1s;
}

.button_lineInvisible {
    height: 2px;
    background-color: none;
}

.hexagons {
    display: flex;
    flex-direction: row;
    width: fit-content;
}

.title {
    @extend .fadeIn;
    font-size: xx-large;
    font-weight: 500;
    margin-bottom: 20px;
    opacity: 0;
}

.subtitle {
    @extend .fadeIn;
    animation-delay: 0.5s;
    color:#7a7a7a;
    font-size:large;
    max-width: 500px;
    text-align: center;
    opacity: 0;
}

.subtitleBottom {
    @extend .fadeIn;
    animation-delay: 1s;
    color:#7a7a7a;
    font-size:large;
    max-width: 500px;
    text-align: center;
    opacity: 0;
}

.watermark {
    position: absolute;
    bottom: 15px;
    right: 15px;
}

