nav {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    padding-top: 25px;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: space-between;
    background-color: #1c1c1c;
    height: 65px;
}

.dummyNav {
    height: 65px;
    width: 100%;
    pointer-events: none;
}

.leftNavigation {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    list-style-type: none;
}

.navigation {
    position: relative;
    z-index: 0;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
}

.navigation > li, .leftNavigation > li {
    text-align: center;
    padding: 9px 45px;
    cursor: pointer;
}

.navigation > li:hover, .leftNavigation > li:hover {
    transform: scale(1.05);
    transition: all 0.1s ease-in-out;
}

.link {
    color: white;
    font-weight: 500;
    text-decoration: none;
    text-transform: uppercase;
}

.leftBlock {
    width: 15px;
    background-color: #DA8E1C;
    height: 100%;
}

.selBar {
    position: absolute;
    border-top-left-radius: 20px ;
    z-index: -1;
    right: 0;
    width: 108%;
    height: 100%;
    background: linear-gradient(90deg, #DA8E1C, #DA8E1C, #E4BC2F);
    clip-path: polygon(34px 0, 100% 0%, 100% 100%, 0% 100%);
    transition: all 0.4s ease-in-out;
}

.selBar_tq {
    @extend .selBar;
    width: 81%;
}

.selBar_half {
    @extend .selBar;
    width: 54%;
}

.selBar_quarter {
    @extend .selBar;
    width: 27%;
}

.fullpageNav_container {
    position: fixed;
    display: flex;
    opacity: 1;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    width: 100%;
    margin-top: 65px;
    height: calc(100vh - 65px);
    background-color: #1c1c1c;
    transition: all 0.4s ease-in-out;
}

.fullpageNav_hidden {
    opacity: 0;
    transition: all 0.4s ease-in-out;
    pointer-events: none;
}

.fullpageNav {
    position: relative;
    margin: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    align-items: center;
    > * {
        cursor: pointer;
    }
    > *:not(:last-child) {
        margin-bottom: 40px;
    }
}

@media only screen and (min-width: 850px) {
    .fullpageNav_container {
        display: none;
    }
}

@media only screen and (max-width: 850px) {
    .navigation > li {
        padding-right: 35px;
    }
    .leftNavigation > li {
        text-align: center;
        padding-left: 15px;
        padding-right: 15px;
    }
}